<template>
  <div id="result">
    <div class="head">
      <span>搜索</span> <span>{{ this.$route.query.cx }}</span
      ><span>，共找到</span> <span>{{count}}</span> <span v-if="activeName === 'first'">首歌曲</span>
      <span v-else-if="activeName === 'second'">位歌手</span>
      <span v-else-if="activeName === 'third'">张专辑</span>
      <span v-else-if="activeName === 'fourth'">个视频</span>
      <span v-else-if="activeName === 'fifth'">个歌单</span>
    </div>
    <div class="main">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <!-- 单曲 -->
        <el-tab-pane label="单曲" name="first">
          <div class="first">
             <div class="one">
              <div></div>
              <div>音乐标题</div>
              <div>歌手</div>
              <div>专辑</div>
              <div>时长</div>
            </div>
            <ul>
              <li v-for="(item, index) in songList" :key="index" @dblclick="playMusic(item.id)">
                <div class="items">
                  <div class="index" v-if="index + 1 < 10">
                    0{{ index + 1 }}
                  </div>
                  <div class="index" v-else>{{ index + 1 }}</div>
                  <div class="title">{{item.name}}</div>
                  <div class="singer">{{item.artists[0].name}}</div>
                  <div class="album">{{item.album.name}}</div>
                  <div class="times">{{item.duration}}</div>
                </div>
              </li>
            </ul>
          </div>
        </el-tab-pane>
        <!-- 歌手 -->
        <el-tab-pane label="歌手" name="second">
          <div class="second">
            <ul>
              <li v-for="(item,index) in singerList" :key="index">
                <div class="items">
                  <img v-lazy="item.img1v1Url" alt="">
                  <div class="name">
                    <span style="color: rgb(197, 197, 197);">{{item.name}}</span> <span v-if="item.alias != ''">{{item.alias[0]}}</span>
                  </div>
                  <div class="icon"><span class="iconfont icon-31wode"></span></div>
                </div>
              </li>
            </ul>
          </div>
        </el-tab-pane>
        <!-- 专辑 -->
        <el-tab-pane label="专辑" name="third">
          <div class="third">
            <ul>
              <li v-for="(item,index) in albumList" :key="index">
                <div class="items">
                  <img v-lazy="item.blurPicUrl" alt="">
                  <div class="works">{{item.name}}<span v-if="item.alias != ''"> ( {{item.alias[0]}} )</span></div>
                  <div class="name"><span style="color: rgb(197, 197, 197);">{{item.artist.name}} </span><span v-if="item.artist.alias != ''">( {{item.artist.alias[0]}} )</span></div>
                </div>
              </li>
            </ul>
          </div>
        </el-tab-pane>
        <!-- 视频 -->
        <el-tab-pane label="视频" name="fourth">
          <div class="fourth">
            <div class="items" v-for="(item, index) in mvLists" :key="index">
              <!-- 上部 -->
              <div class="top" @click="todetailedMV(item.vid)">
                <div class="quantity">
                  <div class="iconfont icon-24gl-play"></div>
                  <div class="number">{{item.playTime}}</div>
                </div>
                <div class="time"><span>{{item.durationms}}</span></div>
                <div class="MVImg">
                  <img :src="item.coverUrl" alt="" />
                </div>
              </div>
              <!-- 底部文字 -->
              <div class="title">
                <div class="name"><span>{{item.title}}</span></div>
                <h5 class="author"><span>{{item.creator[0].userName}}</span></h5>
              </div>
            </div>
          </div>
        </el-tab-pane>
        <!-- 歌单 -->
        <el-tab-pane label="歌单" name="fifth">
          <div class="fifth">
            <ul>
              <li v-for="(item,index) in sheetLists" :key="index" @dblclick="tosongSheet(item.id)">
                <div class="items">
                  <img v-lazy="item.coverImgUrl" alt="">
                  <div class="works">{{item.name}}</div>
                  <div class="quantity"><span>{{item.trackCount}}</span> <span>首</span></div>
                  <div class="name"><span style="color: rgb(109, 109, 109);">by</span> <span>{{item.creator.nickname}}</span></div>
                </div>
              </li>
            </ul>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
    <div class="zhanwei"></div>
    <!-- 返回顶部 -->
    <div class="backT">
      <back-top></back-top>
    </div>
  </div>
</template>

<script>
import backTop from '../components/backTop.vue'
import axios from 'axios'
export default {
  components: {
    backTop
  },
  data () {
    return {
      // tab栏
      activeName: 'first',
      // 查询歌曲
      songList: [],
      // 歌手
      singerList: [],
      // 专辑
      albumList: [],
      // MV
      mvLists: [],
      // 歌单
      sheetLists: [],
      // 搜索数量
      count: 0,
      // 传递过来的查询内容
      cx: this.$route.query.cx
    }
  },
  watch: {
    activeName () {
      // 查询的类型
      let type = 1
      // 获取的数量
      let limit = 10
      switch (this.activeName) {
        case 'first':
          type = 1
          limit = 100
          break
        case 'second':
          type = 100
          limit = 10
          break
        case 'third':
          type = 10
          limit = 30
          break
        case 'fourth':
          type = 1014
          limit = 30
          break
        case 'fifth':
          type = 1000
          limit = 10
          break
      }
      axios({
        url: 'https://autumnfish.cn/search',
        method: 'get',
        params: {
          keywords: this.cx,
          // 查询的类型
          type: type,
          // 获取的数据数量
          limit: limit
        }
      }).then(res => {
        // console.log(res)
        if (res.data.code === 200) {
          if (type === 1) {
            console.log(res)
            this.songList = res.data.result.songs
            // 计算歌曲时间
            for (let i = 0; i < this.songList.length; i++) {
              // 分
              let min = parseInt(this.songList[i].duration / 1000 / 60)
              // 秒
              let sec = parseInt(this.songList[i].duration / 1000 % 60)
              if (min < 10) {
                min = '0' + min
              }
              if (sec < 10) {
                sec = '0' + sec
              }
              this.songList[i].duration = min + ':' + sec
            }
            // 保存搜索数量
            this.count = res.data.result.songCount
          } else if (type === 100) {
            this.singerList = res.data.result.artists
            this.count = res.data.result.artistCount
          } else if (type === 10) {
            this.albumList = res.data.result.albums
            this.count = res.data.result.albumCount
          } else if (type === 1014) {
            this.mvLists = res.data.result.videos
            // 处理播放次数
            for (let j = 0; j < this.mvLists.length; j++) {
              // 处理时间
              // 分
              let min = parseInt(this.mvLists[j].durationms / 1000 / 60)
              // 秒
              let sec = parseInt(this.mvLists[j].durationms / 1000 % 60)
              if (min < 10) {
                min = '0' + min
              }
              if (sec < 10) {
                sec = '0' + sec
              }
              this.mvLists[j].durationms = min + ':' + sec
              // 处理播放次数
              if (this.mvLists[j].playTime > 100000) {
                this.mvLists[j].playTime = parseInt(this.mvLists[j].playTime / 10000) + ' 万'
              }
            }
            this.count = res.data.result.videoCount
          } else if (type === 1000) {
            this.sheetLists = res.data.result.playlists
            this.count = res.data.result.playlistCount
          }
        }
      })
    }
  },
  created () {
    // 获取到传递过来的信息
    console.log(this.$route.query.cx)
    // 歌曲查询
    this.setSongs()
  },
  methods: {
    // tab
    handleClick (tab, event) {
    },
    // 播放歌曲
    playMusic (id) {
    //   console.log(id)
      var URL = 'https://music.163.com/song/media/outer/url?id='
      this.$parent.musicUrl = ''
      this.$parent.musicUrl = URL + id + '.mp3'
    },
    // 歌曲查询
    async setSongs () {
      const { data } = await axios({
        url: 'https://autumnfish.cn/search',
        method: 'get',
        params: {
          keywords: this.cx,
          // 查询的类型
          type: 1,
          // 获取的数据数量
          limit: 30
        }
      })
      // console.log(data)
      if (data.code === 200) {
        this.songList = data.result.songs
        // 计算歌曲时间
        for (var i = 0; i < this.songList.length; i++) {
          // 分
          let min = parseInt(this.songList[i].duration / 1000 / 60)
          // 秒
          let sec = parseInt(this.songList[i].duration / 1000 % 60)
          if (min < 10) {
            min = '0' + min
          }
          if (sec < 10) {
            sec = '0' + sec
          }
          this.songList[i].duration = min + ':' + sec
        }
        // 保存搜索数量
        this.count = data.result.songCount
      }
    },
    // 去歌单详细页面
    tosongSheet (id) {
      // 跳转并携带数据
      this.$router.push('/myMusic/songSheet?id=' + id)
    },
    // 去MV页面
    todetailedMV (id) {
      this.$router.push('/myMusic/detailedMV?mvid=' + id)
      // console.log(id)
    }
  }
}
</script>

<style src='../assets/css/result.less' lang="less" scoped></style>
<style scoped>
#result .backT{
  position: fixed;
  top: 250px;
  right: 50px;
}
#result >>> .el-tabs__item.is-active {
  color: #fff;
  font-size: 18px;
  font-weight: bold;
}
#result >>> .el-tabs__active-bar {
  width: 36px !important;
  background-color: rgb(206, 56, 56);
}
#result >>> .el-tabs__item {
  padding: 0 30px;
  color: rgb(153, 153, 153);
}
#result >>> .el-tabs__nav-wrap::after {
  background-color: #242424;
}
</style>
